<template >
  <div class="grid">
    <h2 class="grid__title">{{langWord.infobar[lang]}}</h2>
    <button class="grid__btn" @click="$emit('changeGrid', !grid)">
      <img src="../assets/img/grid.svg" alt="" v-if="grid" />
      <img src="../assets/img/list.svg" alt="" v-else />
      <span v-if="grid">{{langWord.infobarsearch[lang]}}</span>
      <span v-else>{{langWord.list[lang]}}</span>
    </button>
  </div>
</template>
<script>
export default {
  props: {
    grid: Boolean,
    langWord: Object,
    lang: String,
  },
};
</script>
